@import "../../style/fonts.scss";
@import "../../style/color.scss";

.Tags {
    max-width: 500px;
}

.Tags--listHead {
    @include fonts(13px,16px,700,$color-darkgrey);
}

.Tags--tag {
    @include fonts(12px,14px,normal,$color-grey);
    text-decoration: none;
    &:hover {
        text-decoration: underline;
    }
}